.site-footer {
    padding: 3.6rem 2.4rem;
    background: var(--color-main-500);
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    @include susy-breakpoint($s-320, $c8) {
        flex-direction: column;
        align-items: center;
        gap: 3.2rem;
    }
    &__info {
        display: flex;
        flex-direction: column;
        gap: 1.2rem;
    }
    &__jimshop-logo {
        width: 9.6rem;
        height: 1.67rem;
        background: transparent url('../images/jimshop-logo-w.svg') no-repeat 0 0;
        background-size: cover;
        @include susy-breakpoint($s-320, $c8) {
            margin: 0 auto;
        }
    }
    &__contact-list {
        li {
            font-size: var(--font-size-sm);
            color: #fff;
            line-height: 1.6;
            @include susy-breakpoint($s-320, $c8) {
                text-align: center;
            }
        }
    }
    &__sns {
        display: flex;
        flex-direction: column;
        gap: 1.2rem;
        align-items: flex-end;
        @include susy-breakpoint($s-320, $c8) {
            align-items: center;
        }
    }
    &__sns-list {
        display: flex;
        gap: 0.8rem;
        a {
            display: block;
            width: 2.2rem;
            height: 2.2rem;
            img {
                max-width: 100%;
            }
        }
    }
    &__copyright {
        font-size: var(--font-size-xs);
        color: #fff;
    }
}