.history {
    padding-bottom: 8rem;
    @include susy-breakpoint($s-320, $c8) {
        padding-bottom: 0;
    }
    &__title {
        @extend %site-title;
    }
    &__top-header {
        background-color: #fff;
        border: solid 0.1rem var(--color-grey-100);
        display: flex;
        padding: 2.4rem 3.2rem;
        @include susy-breakpoint($s-320, $c8) {
            padding: 1.2rem 0.8rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            padding: 2rem 2.8rem;
        }
        & > div {
            text-align: center;
            flex: 1;
            font-size: var(--font-size-base);
            color: var(--color-grey-400);
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-xs);
                &:nth-child(3),
                &:nth-child(4),
                &:nth-child(5) {
                    flex: 0 0 6rem;
                }
            }
        }
    }
    &__row {
        background-color: #fff;
        align-items: center;
        display: flex;
        border-right: solid 0.1rem var(--color-grey-100);
        border-left: solid 0.1rem var(--color-grey-100);
        padding: 2.4rem 3.2rem;
        @include susy-breakpoint($s-320, $c8) {
            padding: 1.2rem 0.8rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            padding: 2rem 2.8rem;
        }
        & > div {
            text-align: center;
            flex: 1;
            font-size: var(--font-size-base);
            color: var(--color-grey-500);
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-xs);
                &:nth-child(3),
                &:nth-child(4),
                &:nth-child(5) {
                    flex: 0 0 6rem;
                }
            }
        }
    }
    &__total-price {
        color: var(--color-red-500) !important;
    }
    &__detail {
        display: flex;
        justify-content: center
    }
    &__history-wrap {
        margin-bottom: 8rem;
        display: flex;
        flex-direction: column;
        gap: 2.4rem;
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 4rem;
            gap: 1.6rem;
        }
    }
    &__history-item {
        border-bottom: solid 0.1rem var(--color-grey-100);
    }
    &__detail-content {
        display: none;
    }
    &__btn-detail {
        @extend %site-btn-base;
    }
    &__header {
        background-color: #fff;
        border: solid 0.1rem var(--color-grey-100);
        display: flex;
        padding: 2.4rem 3.2rem;
        @include susy-breakpoint($s-320, $c8) {
            padding: 1.2rem 0.8rem;
        }
        & > div {
            text-align: center;
            flex: 1;
            font-size: var(--font-size-base);
            color: var(--color-grey-400);
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-xs);
                &:nth-child(3),
                &:nth-child(4),
                &:nth-child(5) {
                    flex: 0 0 6rem;
                }
            }
        }
    }
    &__item {
        padding: 2.4rem 3.2rem;
        background-color: #fff;
        display: flex;
        border-right: solid 0.1rem var(--color-grey-100);
        border-left: solid 0.1rem var(--color-grey-100);
        align-items: center;
        &:not(:last-child) {
            border-bottom: solid 0.1rem var(--color-grey-100);
        }
        @include susy-breakpoint($s-320, $c8) {
            padding: 1.2rem 0.8rem;
        }
        & > div {
            flex: 1;
            text-align: center;
            line-height: 1.6;
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-xs);
                &:nth-child(3),
                &:nth-child(4),
                &:nth-child(5) {
                    flex: 0 0 6rem;
                }
            }
        }
    }
    &__product-image {
        img {
            width: 12rem;
            display: block;
            margin: 0 auto;
            @include susy-breakpoint($s-320, $c8) {
                width: 5.6rem;
            }
            @include susy-breakpoint($s-768, $c12) {
                width: 8rem;
            }
        }
    }
    &__product-name {
        a {
            line-height: 1.6;
            font-size: var(--font-size-xl);
            color: var(--color-main-500);
            transition: all 300ms ease;
            &:hover {
                color: var(--color-main-700);
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-sm);
            }
        }
    }
    &__amount {
        font-size: var(--font-size-base);
        color: var(--color-grey-500);
    }
    &__price {
        font-size: var(--font-size-base);
        color: var(--color-grey-500);
    }
    &__select-wrap {
        @extend %site-select;
        width: 8rem;
        margin: 0 auto;
    }
    &__calculate {
        font-size: var(--font-size-base);
        color: var(--color-red-500);
    }
    &__btn-delete {
        font-size: 0;
        width: 2.4rem;
        height: 2.4rem;
        background: transparent url('../images/icon-delete.svg') no-repeat 0 0;
        background-size: cover;
        border: 0;
        padding: 0;
    }
    &__total {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        background-color: #fff;
        padding: 2rem 4rem;
        border: solid 0.1rem var(--color-grey-100);
    }
    &__subtotal-price {
        font-size: var(--font-size-lg);
        color: var(--color-grey-500);
        line-height: 1.6;
    }
    &__total-price {
        font-size: var(--font-size-lg);
        color: var(--color-grey-500);
        line-height: 1.6;
        strong {
            font-size: var(--font-size-3xl);
            color: var(--color-red-500);
            font-weight: 500;
        }
    }
    &__info {
        background-color: #fff;
        border-top: solid 0.1rem var(--color-grey-100);
        border-right: solid 0.1rem var(--color-grey-100);
        border-left: solid 0.1rem var(--color-grey-100);
        padding: 2.4rem 3.2rem;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 2rem;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr;
            padding: 1.2rem 2rem;
            gap: 0.8rem;
        }
        & > div {
            font-size: var(--font-size-sm);
            color: var(--color-grey-500);
            line-height: 1.6;
            strong {
                font-weight: 500;
                display: block;
                font-size: var(--font-size-sm);
                color: var(--color-main-500);
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-xs);
                strong {
                    @include susy-breakpoint($s-320, $c8) {
                        font-size: var(--font-size-xs);
                    }
                }
            }
        }
    }
}
