.account {
    $self: &;
    @extend %site-sm-layout;
    &__title {
        @extend %site-title;
    }
    &__content {
        display: flex;
        flex-direction: column;
        gap: 2rem;
    }
    &__row {
        label {
            @extend %site-label;
        }
        input {
            @extend %site-input;
        }
    }
    &__select-wrap {
        @extend %site-select;
    }
    &__inner-row {
        @extend %site-address;
    }
    &__btn-save,
    &__btn-submit {
        @extend %site-btn-lg;
        width: 100%;
    }
}