.cart {
    padding-bottom: 8rem;
    &__title {
        @extend %site-title;
    }
    &__header {
        background: #fff;
        border: solid 0.1rem var(--color-grey-100);
        display: flex;
        padding: 2.4rem 3.2rem;
        @include susy-breakpoint($s-320, $c8) {
            padding: 1.2rem 0.8rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            padding: 2rem 2.8rem;
        }
        & > div {
            text-align: center;
            flex: 1;
            font-size: var(--font-size-base);
            color: var(--color-grey-400);
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-xs);
                &:nth-child(3),
                &:nth-child(4),
                &:nth-child(5) {
                    flex: 0 0 6rem;
                }
                &:nth-child(6) {
                    flex: 0 0 3rem;
                }
            }
        }
    }
    &__item {
        padding: 2.4rem 3.2rem;
        background-color: #fff;
        display: flex;
        border-right: solid 0.1rem var(--color-grey-100);
        border-left: solid 0.1rem var(--color-grey-100);
        align-items: center;
        &:not(:last-child) {
            border-bottom: solid 0.1rem var(--color-grey-100);
        }
        @include susy-breakpoint($s-320, $c8) {
            padding: 1.2rem 0.8rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            padding: 2rem 2.8rem;
        }
        & > div {
            flex: 1;
            text-align: center;
            line-height: 1.6;
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-xs);
                &:nth-child(3),
                &:nth-child(4),
                &:nth-child(5) {
                    flex: 0 0 6rem;
                }
                &:nth-child(6) {
                    flex: 0 0 3rem;
                }
            }
        }
    }
    &__control {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__product-image {
        img {
            width: 12rem;
            display: block;
            margin: 0 auto;
            @include susy-breakpoint($s-320, $c8) {
                width: 5.6rem;
            }
            @include susy-breakpoint($s-768, $c12) {
                width: 8rem;
            }
        }
    }
    &__product-name {
        a {
            line-height: 1.6;
            font-size: var(--font-size-xl);
            color: var(--color-main-500);
            transition: all 300ms ease;
            &:hover {
                color: var(--color-main-700);
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-sm);
            }
        }
    }
    &__price {
        font-size: var(--font-size-base);
        color: var(--color-grey-500);
    }
    &__select-wrap {
        @extend %site-select;
        width: 8rem;
        margin: 0 auto;
        @include susy-breakpoint($s-320, $c8) {
            width: 4.8rem;
        }
    }
    &__calculate {
        font-size: var(--font-size-base);
        color: var(--color-red-500);
    }
    &__btn-delete {
        font-size: 0;
        width: 2.4rem;
        height: 2.4rem;
        background: transparent url('../images/icon-delete.svg') no-repeat 0 0;
        background-size: cover;
        border: 0;
        padding: 0;
        @include susy-breakpoint($s-320, $c8) {
            width: 2rem;
            height: 2rem;
        }
    }
    &__total {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        background-color: #fff;
        padding: 2rem 4rem;
        border: solid 0.1rem var(--color-grey-100);
        @include susy-breakpoint($s-320, $c8) {
            padding: 1.2rem 1.6rem;
        }
    }
    &__subtotal-price {
        font-size: var(--font-size-lg);
        color: var(--color-grey-500);
        line-height: 1.6;
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-base);
        }
    }
    &__total-price {
        font-size: var(--font-size-lg);
        color: var(--color-grey-500);
        line-height: 1.6;
        strong {
            font-size: var(--font-size-3xl);
            color: var(--color-red-500);
            font-weight: 500;
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-base);
            strong {
                font-size: var(--font-size-2xl);
            }
        }
    }
}

.cart-form {
    $self: &;
    @extend %site-sm-layout;
    display: flex;
    flex-direction: column;
    gap: 4rem;
    margin-bottom: 8rem;
    &__title {
        @extend %site-title;
    }
    &__content {
        display: flex;
        flex-direction: column;
        gap: 2rem;
    }
    &__row {
        label {
            @extend %site-label;
        }
        input {
            @extend %site-input;
        }
    }
    &__checkbox {
        @extend %site-checkbox;
    }
    &__select-wrap {
        @extend %site-select;
    }
    &__inner-row {
        @extend %site-address;
    }
}

.cart-remind {
    @extend %site-sm-layout;
    @include susy-breakpoint($s-320, $c8) {
        padding-left: 2.4rem;
        padding-right: 2.4rem;
    }
    &__title {
        @extend %site-title;
    }
    &__checkbox {
        @extend %site-checkbox;
        margin-bottom: 4rem;
    }
    &__content {
        margin-bottom: 4.8rem;
    }
    &__block {
        &:not(:last-child) {
            margin-bottom: 2.4rem;
        }
        h3 {
            font-size: var(--font-size-xl);
            color: var(--color-grey-500);
            line-height: 1.6;
            margin-bottom: 0.8rem;
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-lg);
            }
        }
        ul {
            padding-left: 2rem;
            list-style: disc;
            li {
                font-size: var(--font-size-base);
                color: var(--color-grey-500);
                line-height: 1.6;
                &:not(:last-child) {
                    margin-bottom: 0.8rem;
                }
                @include susy-breakpoint($s-320, $c8) {
                    font-size: var(--font-size-sm);
                }
            }
        }
        ol {
            padding-left: 2rem;
            list-style: decimal;
            li {
                font-size: var(--font-size-base);
                color: var(--color-grey-500);
                line-height: 1.6;
                &:not(:last-child) {
                    margin-bottom: 0.8rem;
                }
                @include susy-breakpoint($s-320, $c8) {
                    font-size: var(--font-size-sm);
                }
            }
        }
    }
    &__row {
        display: flex;
        justify-content: center;
    }
    &__btn-buy {
        @extend %site-btn-lg;
    }
}