:root {
    font-size: 10px;
    --color-bg: #f8f8f8;
    --color-grey-50: #fcfcfc;
    --color-grey-100: #ECECEC;
    --color-grey-300: #bbbbbb;
    --color-grey-400: #aaaaaa;
    --color-grey-500: #666666;
    --color-main-500: #00B4A4;
    --color-main-700: #009588;
    --color-green-500: #8BBE4A;
    --color-red-500: #CE2D2D;
    --font-size-6xl: 6.4rem;
    --font-size-5xl: 4.8rem;
    --font-size-4xl: 3.6rem;
    --font-size-3xl: 3rem;
    --font-size-2xl: 2.4rem;
    --font-size-xl: 2rem;
    --font-size-lg: 1.8rem;
    --font-size-base: 1.6rem;
    --font-size-sm: 1.4rem;
    --font-size-xs: 1.2rem;
    --border-radius-16: 1.6rem;
    --border-radius-8: 0.8rem;
    --element-margin-bottom: 1.6rem;
}

html {
    font-family: 'Roboto', 'Noto Sans TC', "微軟正黑體", "Microsoft JhengHei", "Arial Unicode MS", "Helvetica Neue", Helvetica, sans-serif;
    &.is-fixed {
        overflow: hidden;
    }
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    background-color: var(--color-bg);
    &.is-fixed {
        overflow: hidden;
    }
}

.wrap {
    margin: 0 auto;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}