.site-header {
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    background-color: #fff;
    height: 7.2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 2.4rem;
    z-index: 200;
    box-shadow: 0 0.2rem 0.8rem rgba(0,0,0,0.1);
    @include susy-breakpoint($s-320, $c8) {
        height: 5.6rem;
        padding-left: 2rem;
        padding-right: 2rem;
    }
    &__jimshop-logo {
        width: 13rem;
        height: 2.25rem;
        background: transparent url('../images/jimshop-logo.svg') no-repeat 0 0;
        background-size: cover;
        font-size: 0;
        @include susy-breakpoint($s-320, $c8) {
            width: 13rem * 0.7;
            height: 2.25rem * 0.7;
        }
    }
    &__nav {
        display: flex;
        align-items: center;
        gap: 1.6rem;
        @include susy-breakpoint($s-320, $c8) {
            gap: 0.8rem;
        }
    }
    &__btn-cart {
        display: flex;
        align-items: center;
        position: relative;
        &::before {
            width: 1.6rem;
            height: 1.6rem;
            background: transparent url('../images/icon-shop.svg') no-repeat 0 0;
            background-size: cover;
            content: "";
        }
        span {
            color: #fff;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: var(--font-size-12);
            width: 2rem;
            height: 2rem;
            border-radius: 50%;
            background-color: var(--color-main-500);
        }
    }
    &__nav-list {
        display: flex;
        gap: 1.6rem;
        a {
            display: block;
            font-size: var(--font-size-base);
            color: var(--color-main-500);
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-sm);
            }
        }
    }
}