.product-list {
    &__title {
        @extend %site-title;
    }
    &__wrap {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 3.2rem;
        margin-bottom: 8rem;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: repeat(2, 1fr);
            gap: 1.6rem;
            padding: 0 2.4rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-template-columns: repeat(3, 1fr);
        }
    }
    &__item {
        display: block;
    }
    &__product-image {
        overflow: hidden;
        img {
            border-radius: 1.6rem;
            max-width: 100%;
            display: block;
            margin: 0 auto 1.2rem;
        }
    }
    &__product-feature {
        font-size: var(--font-size-base);
        line-height: 1.6;
        color: var(--color-grey-500);
        text-align: center;
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-sm);
        }
    }
    &__product-name {
        text-align: center;
        font-size: var(--font-size-lg);
        line-height: 1.6;
        color: var(--color-main-500);
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-base);
            padding-bottom: 0.4rem;
        }
    }
    &__price {
        font-size: var(--font-size-lg);
        text-align: center;
        color: var(--color-red-500);
        strong {
            font-weight: 500;
            font-size: var(--font-size-2xl);
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-base);
            strong {
                font-size: var(--font-size-lg);
            }
        }
    }
}