.login {
    @extend %site-sm-layout;
    &__title {
        @extend %site-title;
    }
    &__content {
        display: flex;
        flex-direction: column;
        gap: 2rem;
    }
    &__third-party {
        display: flex;
        gap: 1.2rem;
        justify-content: center;
    }
    &__btn-facebook,
    &__btn-line,
    &__btn-google {
        font-size: 0;
        width: 3.6rem;
        height: 3.6rem;
        padding: 0;
        border: 0;
        border-radius: 50%;
        @include susy-breakpoint($s-320, $c8) {
            width: 3.2rem;
            height: 3.2rem;
        }
    }
    &__btn-facebook {
        background: transparent url('../images/icon-facebook@3x.png');
        background-size: cover;
    }
    &__btn-line {
        background: transparent url('../images/icon-line@3x.png');
        background-size: cover;
    }
    &__btn-google {
        background: transparent url('../images/icon-google@3x.png');
        background-size: cover;
    }
    &__or {
        font-size: var(--font-size-xs);
        color: var(--color-grey-500);
        display: flex;
        gap: 0.8rem;
        align-items: center;
        &::before,
        &::after {
            content: "";
            flex: 1;
            height: 0.1rem;
            background-color: var(--color-grey-100);
        }
    }
    &__form {
        display: flex;
        flex-direction: column;
        gap: 2rem;
    }
    &__row {
        label {
            @extend %site-label;
        }
        input {
            @extend %site-input;
        }
    }
    &__btn-login {
        @extend %site-btn-lg;
        width: 100%;
    }
    &__helper {
        display: flex;
        justify-content: flex-end;
        gap: 1.2rem;
        a {
            display: block;
            color: var(--color-main-500);
            font-size: var(--font-size-sm);
            transition: all 300ms ease;
            &:hover {
            color: var(--color-main-700);
            }
        }
    }
}