%site-btn-lg {
  background-color: var(--color-main-500);
  border-radius: 4rem;
  padding: 1.2rem 3.2rem;
  font-size: var(--font-size-xl);
  border-radius: 0.8rem;
  color: #fff;
  border: 0;
  display: flex;
  justify-content: center;
  transition: background-color 300ms ease;
  margin: 0;
  &:hover {
    background-color: var(--color-main-700);
  }
  @include susy-breakpoint($s-320, $c8) {
    font-size: var(--font-size-lg);
    border-radius: 0.6rem;
  }
}

%site-btn-base {
  background-color: var(--color-main-500);
  border-radius: 4rem;
  padding: 1rem 2.4rem;
  font-size: var(--font-size-base);
  border-radius: 0.8rem;
  color: #fff;
  border: 0;
  display: flex;
  justify-content: center;
  transition: background-color 300ms ease;
  margin: 0;
  &:hover {
    background-color: var(--color-main-700);
  }
  @include susy-breakpoint($s-320, $c8) {
    font-size: var(--font-size-sm);
    padding: 0.6rem 1.2rem;
    border-radius: 0.6rem;
  }
}

%site-title {
  font-size: var(--font-size-3xl);
  text-align: center;
  line-height: 1.6;
  color: var(--color-grey-500);
  margin-bottom: 1.6rem;
  @include susy-breakpoint($s-320, $c8) {
    font-size: var(--font-size-xl);
  }
  @include susy-breakpoint($s-768, $c12) {
    font-size: var(--font-size-2xl);
  }
}

%site-select {
  box-sizing: border-box;
  width: 100%;
  height: 4.8rem;
  position: relative;
  overflow: hidden;
  @include susy-breakpoint($s-320, $c8) {
    height: 4.4rem;
  }
  &::before {
    width: 1.2rem;
    height: 0.6rem;
    pointer-events: none;
    z-index: 10;
    content: "";
    position: absolute;
    right: 1.6rem;
    top: 50%;
    transform: translate3d(0,-50%,0);
    background: transparent url('../images/icon-arrow.svg') no-repeat 0 0;
    background-size: 1.2rem 0.6rem;
    @include susy-breakpoint($s-320, $c8) {
      right: 0.8rem;
    }
  }
  select {
    background-color: #fff;
    border-radius: 0.8rem;
    border: solid 0.1rem var(--color-grey-100);
    -webkit-appearance: none;
    -moz-appearance: none;
    width: 100%;
    height: 4.8rem;
    font-size: var(--font-size-base);
    padding: 0 1.6rem;
    color: var(--color-main-500);
    transition: all 300ms ease;
    option {
        color: var(--color-grey-500);
    }
    @include susy-breakpoint($s-320, $c8) {
      font-size: var(--font-size-sm);
      height: 4.4rem;
      padding-left: 0.8rem;
      padding-right: 0.8rem;
      border-radius: 0.6rem;
    }
  }
}

%site-checkbox {
  text-align: center;
  input[type=checkbox] {
    position: absolute;
    left: -9999px;
    box-sizing: border-box;
    padding: 0;
  }
  input[type=checkbox]:not(:checked)+label,
  input[type=checkbox]:checked+label {
    position: relative;
    padding-left: 2.8rem;
    cursor: pointer;
    height: 2.2rem;
    line-height: 2.2rem;
    display: inline-block;
    color: var(--color-grey-500);
    font-size: var(--font-size-base);
    @include susy-breakpoint($s-320, $c8) {
      font-size: var(--font-size-sm);
    }
  }
  input[type=checkbox]:not(:checked)+label:before,
  input[type=checkbox]:checked+label:before {
    content: '';
    position: absolute;
    left: 0;
    border-radius: 0.6rem;
    width: 2.2rem;
    height: 2.2rem;
    background-color: var(--color-main-500);
    border-radius: 0.6rem;
    top: 0;
    box-sizing: border-box;
  }
  input[type=checkbox]+label:after {
    content: "";
    width: 1rem;
    height: 0.6rem;
    background: transparent url(../images/icon-check.svg) no-repeat center center;
    background-size: 1rem 0.6rem;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate3d(0.6rem, -60%, 0);
    border: solid 0 transparent;
    transition: all 300ms ease;
    opacity: 0;
  }
  input[type=checkbox]:checked+label:after {
    opacity: 1;
  }
}

%site-label {
  display: block;
  font-size: var(--font-size-base);
  color: var(--color-grey-500);
  line-height: 1.6;
  margin-bottom: 0.4rem;
  @include susy-breakpoint($s-320, $c8) {
    font-size: var(--font-size-sm);
  }
}

%site-input {
  background-color: #fff;
  border-radius: 0.8rem;
  border: solid 0.1rem var(--color-grey-100);
  -webkit-appearance: none;
  -moz-appearance: none;
  width: 100%;
  height: 4.8rem;
  font-size: var(--font-size-base);
  padding: 0 1.6rem;
  color: var(--color-main-500);
  transition: all 300ms ease;
  &::placeholder {
      color: var(--color-grey-300);
  }
  @include susy-breakpoint($s-320, $c8) {
    font-size: var(--font-size-sm);
    height: 4.4rem;
    padding-left: 0.8rem;
    padding-right: 0.8rem;
    border-radius: 0.6rem;
  }
}

%site-address {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 0.8rem;
  input {
      grid-column-start: 1;
      grid-column-end: 3;
      grid-row-start: 2;
      grid-row-end: 3;
  }
}

%site-sm-layout {
  max-width: 48rem;
  margin: 0 auto;
  @include susy-breakpoint($s-320, $c8) {
    width: 100%;
    padding-left: 2.4rem;
    padding-right: 2.4rem;
  }
}