.site-main {
    flex-grow: 1;
    width: 120rem;
    padding-top: 16rem;
    padding-bottom: 8rem;
    margin: 0 auto;
    @include susy-breakpoint($s-320, $c8) {
        width: 100%;
        padding-top: 12rem;
    }
    @include susy-breakpoint($s-768, $c12) {
        width: 100%;
    }
    @include susy-breakpoint($s-960, $c12) {
        width: 92rem;
    }
}