.site-pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    @include susy-breakpoint($s-320, $c8) {
        flex-wrap: wrap;
    }
    &__page {
        display: flex;
        gap: 0.8rem;
        align-items: center;
    }
    &__btn-page {
        font-size: var(--font-size-sm);
        color: var(--color-main-500);
        border: solid 0.1rem var(--color-main-500);
        padding: 1rem 1.2rem;
        border-radius: 0.4rem;
        background-color: #fff;
        display: block;
        transition: all 300ms ease;
        &:hover {
            background-color: var(--color-main-500);
            color: #fff;
        }
        &.active {
            background-color: var(--color-main-500);
            color: #fff;
            pointer-events: none;
        }
        @include susy-breakpoint($s-320, $c8) {
            padding: 0.8rem 1rem;
            font-size: var(--font-size-xs);
        }
    }
    &__status {
        font-size: var(--font-size-sm);
        color: var(--color-main-500);
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-xs);
        }
    }
}