.product {
    &__wrap {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        padding: 0 8rem 12rem;
        gap: 8rem;
        @include susy-breakpoint($s-320, $c8) {
            gap: 2rem;
            padding-left: 2rem;
            padding-right: 2rem;
            padding-bottom: 4rem;
            grid-template-columns: 1fr;
        }
    }
    &__product-image {
        overflow: hidden;
        img {
            border-radius: 2.4rem;
            max-width: 100%;
            display: block;
            margin: 0;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 26rem;
            margin: 0 auto;
        }
    }
    &__detail {
        display: flex;
        flex-direction: column;
        gap: 2.4rem;
    }
    &__basic-info {
        padding-bottom: 2.4rem;
        border-bottom: 0.1rem solid var(--color-grey-100);
    }
    &__product-feature {
        font-size: var(--font-size-lg);
        line-height: 1.6;
        color: var(--color-grey-500);
    }
    &__product-name {
        font-size: var(--font-size-3xl);
        line-height: 1.6;
        color: var(--color-main-500);
    }
    &__product-specification {
        font-size: var(--font-size-base);
        line-height: 1.6;
        color: var(--color-grey-500);
    }
    &__intro {
        padding-bottom: 2.4rem;
        border-bottom: 0.1rem solid var(--color-grey-100);
        h2 {
            font-size: var(--font-size-xl);
            color: var(--color-grey-500);
            margin-bottom: 1.6rem;
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-lg);
            }
        }
        p {
            font-size: var(--font-size-base);
            line-height: 1.6;
            color: var(--color-grey-500);
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-sm);
            }
        }
    }
    &__shop {
        display: flex;
        flex-direction: column;
        gap: 2rem;
    }
    &__amount {
        display: flex;
        align-items: baseline;
        gap: 2rem;
        label {
            font-size: var(--font-size-base);
            color: var(--color-grey-500);
            margin-bottom: 1.6rem;
            white-space: nowrap;
            display: block;
        }
    }
    &__select-wrap {
        @extend %site-select;
    }
    &__price-wrap {
        display: flex;
        justify-content: flex-end;
        align-items: baseline;
        gap: 1.6rem;
        @include susy-breakpoint($s-320, $c8) {
            justify-content: center;
        }
    }
    &__origin-price {
        font-size: var(--font-size-xl);
        color: var(--color-grey-500);
    }
    &__price {
        color: var(--color-red-500);
        font-size: var(--font-size-xl);
        strong {
            font-weight: 500;
            font-size: var(--font-size-4xl);
        }
    }
    &__control {
        display: flex;
        justify-content: flex-end; 
        gap: 1.6rem;
        @include susy-breakpoint($s-320, $c8) {
            justify-content: center;
        }
    }
    &__btn-add {
        @extend %site-btn-lg;
    }
    &__btn-buy {
        @extend %site-btn-lg;
    }
    &__content {
        max-width: 64rem;
        margin: 0 auto;
        padding-bottom: 3.2rem;
        @include susy-breakpoint($s-320, $c8) {
            padding-left: 2rem;
            padding-right: 2rem;
        }
        h2 {
            font-size: var(--font-size-xl);
            color: var(--color-grey-500);
            text-align: center;
            line-height: 1.6;
            margin-bottom: 1.6rem;
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-lg);
            }
        }
        p {
            color: var(--color-grey-500);
            font-size: var(--font-size-base);
            line-height: 1.6;
            margin-bottom: 1.6rem;
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-sm);
            }
        }
        img {
            max-width: 100%;
            display: block;
            margin: 0 auto 1.6rem;
            border-radius: 1.6rem;
        }
    }
}

.product-recommend {
    max-width: 64rem;
    margin: 0 auto;
    @include susy-breakpoint($s-320, $c8) {
        padding-left: 2rem;
        padding-right: 2rem;
    }
    &__title {
        @extend %site-title;
    }
    &__wrap {
        display: flex;
        gap: 4rem;
        @include susy-breakpoint($s-320, $c8) {
            gap: 1.6rem;
        }
    }
    &__product-image {
        overflow: hidden;
        img {
            border-radius: 1.6rem;
            max-width: 100%;
            display: block;
            margin: 0 auto 1.2rem;
        }
    }
    &__product-feature {
        font-size: var(--font-size-base);
        line-height: 1.6;
        color: var(--color-grey-500);
        text-align: center;
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-sm);
        }
    }
    &__product-name {
        text-align: center;
        font-size: var(--font-size-lg);
        line-height: 1.6;
        color: var(--color-main-500);
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-base);
            padding-bottom: 0.4rem;
        }
    }
    &__price {
        font-size: var(--font-size-lg);
        text-align: center;
        color: var(--color-red-500);
        strong {
            font-weight: 500;
            font-size: var(--font-size-2xl);
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-base);
            strong {
                font-size: var(--font-size-lg);
            }
        }
    }
}